@import "mixins";


@include font-face('pfdintextcondpro-light',  './../fonts/pfdintextcondpro-light-web',  normal, normal, eot svg ttf woff);
@include font-face('pfdintextcondpro-medium', './../fonts/pfdintextcondpro-medium-web', normal, normal, eot svg ttf woff);

* {
 box-sizing: border-box;
}

html,body {
 margin: 0;
 padding: 0;
 height: 100%;
}
.main-container {
 display: flex;
 flex-wrap: nowrap;
 height: 100%;
}

.navbar {
 width: 350px;
 height: 100%;
 overflow: auto;
 font-family: 'pfdintextcondpro-light';
 font-size: 17px;
 line-height: 21px;

 &__title {
  background: #2980B9;
  padding: 30px 20px;
  color: #fcfcfc;

  h1 {
   padding: 0;
   margin: 0;
   font-size: 32px;
  }
 }

 &__points {
  background: #4e4a4a;
  padding: 10px 0;
 }

 &__point {
  padding: 4px 40px 4px 20px;

  &:hover {
   background: #838383;
  }
  &.current {
   background: #fcfcfc;
   color: #404040 !important;
  }
 }

 a {
  color: #fcfcfc !important;
  text-decoration: none;
 }
}


.frame {
 width: 100%;
 height: 100%;
}
